	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2006-11-09-stuff-from-microsoft-labs/index.md absPath of file >>> MarkdownRemark","html":"<p>Here are a couple of interesting things coming out of MS:</p>\n<p>Photosynthe - pretty cool. Creates a point cloud in 3D of a group of photos. No human intervention required. It looks at your photos and puts them together - in panormas. It figures out details. Lets you zoom in. It is pretty cool. All this is done in a web browser. Offline the server analyzes photos, matches them up, lets you zoom in, move around. It figures out camera angles and puts in triangles that show where the photographer was standing when they took the picture. The image processing is amazing.</p>\n<p>Virtual Earth 3D - 3d version of maps like google earth.</p>","frontmatter":{"title":"Stuff from Microsoft labs","date":"November 09, 2006","imageUrl":null,"image":null}}},"pathContext":{"path":"/stuff-from-microsoft-labs"}}

